export default {
    id: "sogou",
    name: "搜狗",
    home: "https://www.sogou.com",
    getSearchURI(keyword, page, pageSize) {
        if (page > 1) {
            return `${this.home}/web?&ie=utf8&query=${keyword}&page=${page}`
        } else {
            return `${this.home}/web?&ie=utf8&query=${keyword}`
        }
    },
    getResultElement(document) {
        //去除广告
        for (let child of document.querySelectorAll("[id^='ad_result_']")) {
            child.remove();
        }
        document.getElementById("upquery").disabled = true;
        document.getElementById("upquery_reset").style.display = "none";
        document.getElementById("right").style.display = "none";
        return document.querySelectorAll("#main>div>.results>.vrwrap, #main>div>.results>.rb");
    },
    getLinkData(element) {
        let a = element.querySelector("h3 > a");
        if (a) {
            return {
                eng_id: this.id,
                eng_name: this.name,
                title: a.innerText,
                href: a.href
            };
        } else {
            return null;
        }
    }
}
