<template>
    <div class="result-link-list">
        <div class="options">
            <div class="left">
            </div>
            <div class="right">
                <Select v-model="params.status" placeholder="请选择结果检索状态" clearable style="width:200px">
                    <Option v-for="(value, key) in statues" :value="key" :key="key">{{value}}</Option>
                </Select>
                <Button type="primary" icon="md-refresh" @click="listResultLink(id)">查 询</Button>
                <Button icon="md-refresh" @click="resetParams">重 置</Button>
            </div>
        </div>
        <Table border :columns="cols" :data="list">
            <template slot-scope="{ row }" slot="status">
                <Tag :color="getStatusColor(row.status)">{{getStatusText(row.status)}}</Tag>
            </template>
            <template slot-scope="{ row }" slot="content">
                <span class="href" @click="toUrl(row.content)">{{row.content}}</span>
            </template>
        </Table>
        <Page
            :page-size="pageSize"
            :total="total"
            show-sizer
            show-total
            @on-change="pageChange"
            @on-page-size-change="pageSizeChange"/>
    </div>
</template>

<script>
    import {statusColor, statusText} from "@js/constants";

    export default {
        name: "result-link-list",
        data() {
            return {
                statues: statusText,
                id: null,
                params: {
                    result_id: null,
                    status: null
                },
                cols: [
                    {
                        title: '链接ID',
                        key: 'id',
                        align: 'center',
                        width: 100
                    },
                    {
                        title: '链接地址',
                        slot: 'content',
                        align: 'center'
                    },
                    {
                        title: '链接状态',
                        slot: "status",
                        align: 'center',
                        width: 120
                    }
                ],
                list: [],
                page: 1,
                pageSize: 20,
                total: 0
            }
        },
        methods: {
            resetParams() {
                this.params.status = null;
                this.resetPage();
                this.listResultLink(this.id);
            },
            toUrl(url) {
                electronAPI.invoke({type: "openInnerWindow", content: {url}})
            },
            getStatusText(status) {
                return statusText[status];
            },
            getStatusColor(status) {
                return statusColor[status];
            },
            listResultLink(id) {
                const {params, page, pageSize} = this;
                this.id = params.result_id = id;
                electronAPI.invoke({
                    type: "listResultLink", content: {
                        params,
                        page,
                        pageSize,
                    }
                }).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                })
            },
            pageChange(page) {
                this.page = page;
                this.listResultLink(this.id);
            },
            pageSizeChange(pageSize) {
                this.pageSize = pageSize;
                this.listResultLink(this.id);
            },
            resetPage() {
                this.page = 1;
                this.total = 0;
            }
        }
    }
</script>

<style scoped lang="less">
    @import "~@style/variables.less";
    @import "~@style/mixins.less";

    .result-link-list {
        .options {
            padding-bottom: @padding-size;
            display: flex;
            justify-content: space-between;

            .left, .right {
                display: flex;

                & > * {
                    margin-right: @margin-size;
                }

                & > *:last-child {
                    margin-right: unset;
                }
            }
        }

        .ivu-page {
            margin-top: @margin-size;
            text-align: right;
        }

        .href {
            color: blue;
            text-decoration: underline;
            cursor: pointer;
            .text-nowrap
        }
    }
</style>
