export default {
    id: "baidu",
    name: "百度",
    home: "https://www.baidu.com",
    getSearchURI(keyword, page, pageSize) {
        //百度没有多页
        if (pageSize !== 10) {
            pageSize = 10;
        }
        if (page > 1) {
            page = (page - 1) * pageSize;
            return `${this.home}/s?ie=UTF-8&wd=${keyword}&base_query=${keyword}&oq=${keyword}&usm=2&pn=${page}`
        } else {
            return `${this.home}/s?ie=UTF-8&wd=${keyword}`
        }
    },
    getResultElement(document) {
        let content = document.getElementById("content_left");
        if (!content) {
            return [];
        }
        //去除广告
        for (let child of content.children) {
            const ad = child.querySelector("a.c-gap-left");
            if (child.classList.length === 0 || (ad && ad.innerHTML.includes("广告"))) {
                child.remove();
            }
        }
        document.getElementById("kw").disabled = true;
        document.querySelector("#form i.quickdelete").style.display = "none";
        document.getElementById("content_right").style.display = "none";
        return content.querySelectorAll(".result-op.c-container, .result.c-container");
    },
    getLinkData(element) {
        let a = element.querySelector(".c-title > a");
        if (a) {
            return {
                eng_id: this.id,
                eng_name: this.name,
                title: a.innerText,
                href: a.href
            };
        } else {
            return null;
        }
    }
}
