<template>
    <div class="result-list">
        <div class="options">
            <div class="left">
                <Button v-if="showBack" icon="md-arrow-back" @click="back">返 回</Button>
                <Button type="primary" icon="md-add" @click="addResult">添 加</Button>
                <Button type="success" icon="md-download" @click="exportResult">导 出</Button>
                <Button type="warning" icon="md-refresh" @click="restartTask">重启队列</Button>
            </div>
            <div class="right">
                <Select v-model="params.eng_id" placeholder="请选择结果来源" clearable style="width:200px">
                    <Option value="custom">自定义链接</Option>
                    <Option v-for="(eng, index) in engines" :value="eng.id" :key="index">{{eng.name}}</Option>
                </Select>
                <Select v-model="params.status" placeholder="请选择结果检索状态" clearable style="width:200px">
                    <Option v-for="(value, key) in statues" :value="key" :key="key">{{value}}</Option>
                </Select>
                <Button type="primary" icon="md-search" @click="listResult">查 询</Button>
                <Button icon="md-refresh" @click="resetParams">重 置</Button>
            </div>
        </div>
        <Table border :columns="cols" :data="list">
            <template slot-scope="{ row }" slot="status">
                <Tag :color="getStatusColor(row.status)">{{getStatusText(row.status)}}</Tag>
            </template>
            <template slot-scope="{ row }" slot="href">
                <span class="href" @click="toUrl(row.href)">{{row.href}}</span>
            </template>
            <template slot-scope="{ row }" slot="title">
                <span class="title">{{row.title}}</span>
            </template>
            <template slot-scope="{ row }" slot="count">
                <Tag color="default" v-if="row.wait_link_count > 0">
                    <Icon type="md-cafe"/>
                    {{row.wait_link_count}}
                </Tag>
                <Tag color="error" v-if="row.fail_link_count > 0">
                    <Icon type="md-close"/>
                    {{row.fail_link_count}}
                </Tag>
                <Tag color="success" v-if="row.finish_link_count > 0">
                    <Icon type="md-checkmark"/>
                    {{row.finish_link_count}}
                </Tag>
                <Tag color="primary">
                    <Icon type="md-archive"/>
                    {{row.total_link_count}}
                </Tag>
            </template>
            <template slot-scope="{ row, index }" slot="action">
                <div class="actions">
                    <Button type="primary" icon="md-list" size="small"
                            @click="showDataDetail(row)">数据
                    </Button>
                    <Button type="primary" icon="md-link" size="small"
                            @click="showLinkDetail(row)">链接
                    </Button>
                    <Button type="warning" v-if="row.status < 2"
                            icon="md-fastforward" size="small"
                            @click="skipResult(row)">跳过
                    </Button>
                    <Button type="error" icon="md-close" size="small"
                            @click="removeResult(row)">删除
                    </Button>
                </div>
            </template>
        </Table>
        <Page :page-size="pageSize"
              :total="total"
              show-sizer
              show-total
              @on-change="pageChange"
              @on-page-size-change="pageSizeChange"/>
        <Drawer title="检索详情" :width="width" :closable="false" v-model="detailVisible">
            <result-data-list ref="data-list" v-show="detailType === 0"/>
            <result-link-list ref="link-list" v-show="detailType === 1"/>
        </Drawer>
        <Modal
            v-model="showAddResult"
            title="添加自定义链接"
            @on-ok="doAddResult"
            @on-cancel="form.input = null">
            <Input v-model="form.input"
                   type="textarea"
                   :rows="4"
                   autofocus
                   placeholder="请输入需要检索的链接地址，多个链接使用[ , ]隔开"/>
        </Modal>
    </div>
</template>

<script>
    import {statusColor, statusText} from "@js/constants";
    import ResultLinkList from "@cmp/result-link-list";
    import ResultDataList from "@cmp/result-data-list";
    import StringUtils from "@js/utils/string-utils";
    import engines from "@js/engines";

    export default {
        name: "result-list",
        components: {ResultDataList, ResultLinkList},
        props: {
            showBack: true
        },
        data() {
            return {
                statues: statusText,
                engines,
                params: {
                    eng_id: null,
                    status: null
                },
                showAddResult: false,
                detailType: 0,
                detailVisible: false,
                cols: [
                    {
                        title: '结果ID',
                        key: 'id',
                        width: 100
                    },
                    {
                        title: '结果来源',
                        key: 'eng_name',
                        align: 'center',
                        width: 120
                    },
                    {
                        title: '结果标题',
                        slot: 'title'
                    },
                    {
                        title: '结果链接',
                        slot: 'href'
                    },
                    {
                        title: '检索状态',
                        slot: "status",
                        align: 'center',
                        width: 120
                    },
                    {
                        title: '检索结果',
                        slot: "count",
                        align: 'center'
                    },
                    {
                        title: '操作',
                        slot: 'action',
                        align: 'center',
                        width: 200
                    }
                ],
                form: {
                    input: null
                },
                list: [],
                page: 1,
                pageSize: 10,
                total: 0
            }
        },
        computed: {
            width() {
                return window.screen.availWidth - 400;
            }
        },
        methods: {
            restartTask() {
                electronAPI.invoke({
                    type: 'restartTask'
                }).then(() => {
                    this.$Message.success("操作成功");
                });
            },
            back() {
                this.$router.push("/");
            },
            resetParams() {
                this.params.eng_id = null;
                this.params.status = null;
                this.resetPage();
                this.listResult();
            },
            addResult() {
                this.showAddResult = true;
            },
            exportResult() {
                electronAPI.invoke({
                    type: 'getFilePath'
                }).then(({canceled, filePath}) => {
                    if (canceled) {
                        this.$Message.error("导出已取消");
                    } else {
                        electronAPI.invoke({
                            type: 'exportResult', content: filePath
                        }).then(() => {
                            this.$Message.success("数据表格导出成功");
                        });
                    }
                });
            },
            doAddResult() {
                const {input} = this.form;
                if (StringUtils.isBlank(input)) {
                    this.form.input = null;
                    return;
                }
                const links = input.split(",");
                const results = links.map(link => {
                    return {
                        eng_id: "custom",
                        eng_name: "自定义链接",
                        title: null,
                        href: link
                    }
                })
                electronAPI.invoke({
                    type: 'saveResultBatch', content: results
                }).then(() => {
                    this.$Message.success("添加成功");
                    this.resetPage();
                    this.listResult();
                    this.form.input = null;
                });
            },
            skipResult({id}) {
                electronAPI.invoke({type: 'skipResult', content: id}).then(res => {
                    this.$Message.success("操作成功");
                    this.listResult();
                })
            },
            removeResult({id}) {
                electronAPI.invoke({type: 'removeResult', content: id}).then(res => {
                    this.$Message.success("操作成功");
                    this.listResult();
                })
            },
            showLinkDetail({id}) {
                this.detailType = 1;
                this.detailVisible = true;
                const $ref = this.$refs["link-list"];
                if ($ref) {
                    $ref.resetPage();
                    $ref.listResultLink(id);
                }
            },
            showDataDetail({id}) {
                this.detailType = 0;
                this.detailVisible = true;
                const $ref = this.$refs["data-list"];
                if ($ref) {
                    $ref.resetPage();
                    $ref.listResultData(id);
                }
            },
            listResult() {
                const {params, page, pageSize} = this;
                electronAPI.invoke({type: "listResult", content: {params, page, pageSize}}).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                })
            },
            toUrl(url) {
                electronAPI.invoke({type: "openInnerWindow", content: {url}})
            },
            getStatusText(status) {
                return statusText[status];
            },
            getStatusColor(status) {
                return statusColor[status];
            },
            pageChange(page) {
                this.page = page;
                this.listResult();
            },
            pageSizeChange(pageSize) {
                this.pageSize = pageSize;
                this.listResult();
            },
            resetPage() {
                this.page = 1;
                this.total = 0;
            }
        }
    }
</script>

<style scoped lang="less">
    @import "~@style/variables.less";
    @import "~@style/mixins.less";

    .result-list {
        .options {
            padding-bottom: @padding-size;
            display: flex;
            justify-content: space-between;

            .left, .right {
                display: flex;

                & > * {
                    margin-right: @margin-size;
                }

                & > *:last-child {
                    margin-right: unset;
                }
            }
        }

        .title {
            .text-nowrap
        }

        .ivu-page {
            margin-top: @margin-size;
            text-align: right;
        }

        .href {
            color: blue;
            text-decoration: underline;
            cursor: pointer;
            .text-nowrap
        }

        .actions {
            padding: 5px;

            .ivu-btn {
                margin: 0 5px 5px 0;
            }
        }
    }
</style>
