export default {
    id: "bing",
    name: "必应",
    home: "https://cn.bing.com",
    getSearchURI(keyword, page, pageSize) {
        //百度没有多页
        if (pageSize !== 10) {
            pageSize = 10;
        }
        if (page > 1) {
            page = (page - 1) * pageSize;
            return `${this.home}/search?q=${keyword}&qs=n&first=${page}`
        } else {
            return `${this.home}/search?q=${keyword}`
        }
    },
    getResultElement(document) {
        document.getElementById("sb_form_q").disabled = true;
        document.getElementById("sb_clt").style.display = "none";
        let content = document.getElementById("b_results");
        if (!content) {
            return [];
        }
        return content.querySelectorAll(".b_algo");
    },
    getLinkData(element) {
        let a = element.querySelector("h2 > a");
        if (a) {
            return {
                eng_id: this.id,
                eng_name: this.name,
                title: a.innerText,
                href: a.href
            };
        } else {
            return null;
        }
    }
}
