<template>
    <div class="result-data-list">
        <div class="options">
            <div class="left">
            </div>
            <div class="right">
                <Select v-model="params.type" placeholder="请选择数据类型" clearable style="width:200px">
                    <Option v-for="(value, key) in linkDataType" :value="key" :key="key">{{value}}</Option>
                </Select>
                <Button type="primary" icon="md-refresh" @click="listResultData(id)">查 询</Button>
                <Button icon="md-refresh" @click="resetParams">重 置</Button>
            </div>
        </div>
        <Table border :columns="cols" :data="list">
            <template slot-scope="{ row }" slot="type">
                <Tag color="primary">{{getTypeText(row.type)}}</Tag>
            </template>
            <template slot-scope="{ row }" slot="content">
                <span class="href" @click="openContent(row)">{{row.content}}</span>
            </template>
            <template slot-scope="{ row }" slot="link">
                <span class="href" @click="toUrl(row.link)">{{row.link}}</span>
            </template>
        </Table>
        <Page
            :page-size="pageSize"
            :total="total"
            show-sizer
            show-total
            @on-change="pageChange"
            @on-page-size-change="pageSizeChange"/>
    </div>
</template>

<script>
    import {linkDataType} from "@js/constants";

    export default {
        name: "result-data-list",
        data() {
            return {
                linkDataType,
                id: null,
                params: {
                    result_id: null,
                    type: null
                },
                cols: [
                    {
                        title: '数据ID',
                        key: 'id',
                        align: 'center',
                        width: 100
                    },
                    {
                        title: '数据来源',
                        slot: "link",
                        align: 'center',
                        width: 300
                    },
                    {
                        title: '数据类型',
                        slot: "type",
                        align: 'center',
                        width: 120
                    },
                    {
                        title: '数据内容',
                        slot: 'content',
                        align: 'center'
                    }
                ],
                list: [],
                page: 1,
                pageSize: 20,
                total: 0
            }
        },
        methods: {
            openContent(data) {
                electronAPI.invoke({
                    type: "openContent", content: data
                }).then(res => {
                    const {type, content} = res.content;
                    this.$Message[type]({content});
                })
            },
            resetParams() {
                this.params.type = null;
                this.resetPage();
                this.listResultData(this.id);
            },
            toUrl(url) {
                electronAPI.invoke({type: "openInnerWindow", content: {url}})
            },
            getTypeText(type) {
                return linkDataType[type];
            },
            listResultData(id) {
                const {params, page, pageSize} = this;
                this.id = params.result_id = id;
                electronAPI.invoke({
                    type: "listResultData", content: {
                        params,
                        page,
                        pageSize,
                    }
                }).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                })
            },
            pageChange(page) {
                this.page = page;
                this.listResultData(this.id);
            },
            pageSizeChange(pageSize) {
                this.pageSize = pageSize;
                this.listResultData(this.id);
            },
            resetPage() {
                this.page = 1;
                this.total = 0;
            }
        }
    }
</script>

<style scoped lang="less">
    @import "~@style/variables.less";
    @import "~@style/mixins.less";

    .result-data-list {
        .options {
            padding-bottom: @padding-size;
            display: flex;
            justify-content: space-between;

            .left, .right {
                display: flex;

                & > * {
                    margin-right: @margin-size;
                }

                & > *:last-child {
                    margin-right: unset;
                }
            }
        }

        .ivu-page {
            margin-top: @margin-size;
            text-align: right;
        }

        .href {
            color: blue;
            text-decoration: underline;
            cursor: pointer;
            .text-nowrap
        }
    }
</style>
